import React from 'react'
import { useForm, ValidationError } from '@formspree/react';
import '../styles/PortfolioStyle.css'

const ContactForm = () => {
  const [state, handleSubmit] = useForm("xyyldgjr");
  if (state.succeeded) {
      return <p>Thanks for joining!</p>;
  }
  return (
      <form onSubmit={handleSubmit}>
      <label htmlFor="email">
        Email Address:
      </label>
      <input
        id="email"
        type="email" 
        name="email"
      />
      <ValidationError 
        prefix="Email" 
        field="email"
        errors={state.errors}
      />
      <label htmlFor="message">
        Message:
      </label>
      <textarea
        id="message"
        name="message"
        rows="10" 
        cols="100"
      />
      <ValidationError 
        prefix="Message" 
        field="message"
        errors={state.errors}
      />
      <button type="submit" disabled={state.submitting}>
        Submit
      </button>
    </form>
  );
}

const Contact = () => {
    return (
        <section id="contact">
            <h2>Contact Information</h2>
            <p>To contact me reach to jabauer.1998@gmail.com or use the form below:  </p>
            <ContactForm />
        </section>
    )
}

export default Contact