import React from 'react'
import github from '../img/Githubicon.png'

const Projectdef = ({title, paragraph}) => {
    const link  = "https://www.github.com/h20man13/" + title;
    return (
        <li className="projdef">
            <div className="alignDiv">
                <h3>{title}</h3>
                <a href={link} target="_blank"><img src={github} alt={link}></img></a>
            </div>
            <p>{paragraph}</p>
        </li>
    )
}

export default Projectdef
